import React from "react";
import "./App.css";
import Header from "./landing/Header/Header";
import Main from "./landing/Main/Main";
import Footer from "./landing/Footer/Footer";
import { CatalogContext, numenclatura } from "../context/CatalogContext";
import Popup from "./landing/Popup/Popup";
import PopupFeedback from "./landing/PopupFeedback/PopupFeedback";
import jalousie from "./utils/jalousie";
import normalize from "../styles/normalize.css";
function App() {
  const [art, setArt] = React.useState("");
  const [isCatalogeOpen, setCatalogOpen] = React.useState(false);
  const [parentId, setParentId] = React.useState("");
  const [isPopupOpen, setPopupOpen] = React.useState(false);
  const [isPopupFeedbackOpen, setPopupFeedbackOpen] = React.useState(false);
  function closeCatalog() {
    setCatalogOpen(false);
    setArt("");
  }

  function handleClickCataloge(e) {
    if (isCatalogeOpen & (e.target.id !== art)) {
      setArt(e.target.id);
      setParentId(e.target.parentElement.parentElement.id);
    } else {
      setCatalogOpen(!isCatalogeOpen);
      setArt(e.target.id);
      setParentId(e.target.parentElement.parentElement.id);
    }
    console.log(art);
  }
  function handleClickCatalogeImg(e) {
    setArt(e.target.id);
    console.log(isCatalogeOpen);
  }
  function handleClickPopupOpen() {
    setPopupOpen(!isPopupOpen);
  }
  function handleClickPopupFeedbackOpen() {
    setPopupFeedbackOpen(!isPopupFeedbackOpen);
  }
  function closePopup() {
    setPopupOpen(!isPopupOpen);
  }
  function closePopupFeedback() {
    setPopupFeedbackOpen(!isPopupFeedbackOpen);
  }
  function closeInfo() {
    setCatalogOpen(!isCatalogeOpen);
  }

  return (
    <div className="page">
      <CatalogContext.Provider value={numenclatura[art]}>
        <Header />
        <Main
          isOpen={isCatalogeOpen}
          onClick={handleClickCataloge}
          onClickImg={handleClickCatalogeImg}
          onClose={closeCatalog}
          parentId={parentId}
          boxId={art}
          onPopup={handleClickPopupOpen}
          onPopupFeedback={handleClickPopupFeedbackOpen}
          onCloseInfo={closeInfo}
        />
        <Footer />
        <Popup onClose={closePopup} isOpen={isPopupOpen} />
        <PopupFeedback
          onClose={closePopupFeedback}
          isOpen={isPopupFeedbackOpen}
        />
      </CatalogContext.Provider>
    </div>
  );
}

export default App;
